              <el-radio-button label="small">  </el-radio-button>
              <el-radio-button label="mini">  </el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="formConf_.labelPosition">
              <el-radio-button label="left">  </el-radio-button>
              <el-radio-button label="right">  </el-radio-button>
              <el-radio-button label="top">  </el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-input-number
              v-model="formConf_.labelWidth"
              placeholder=""
            />
          </el-form-item>
          <el-form-item label="">
            <el-input-number
              v-model="formConf_.gutter"
              :min="0"
              placeholder=""
            />
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="formConf_.disabled" />
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="formConf_.formBtns" />
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="formConf_.unFocusedComponentBorder" />
          </el-form-item>
        </el-form>
      </el-scrollbar>
    </div>

    <treeNode-dialog
      :visible.sync="dialogVisible"
      title=""
      @commit="addNode"
    />
    <icons-dialog
      :visible.sync="iconsVisible"
      :current="activeData_[currentIconModel]"
      @select="setIcon"
    />
  </div>
</template>

<script>
// import { isArray } from "util";
import draggable from "vuedraggable";
import TreeNodeDialog from "./TreeNodeDialog";
import { isNumberStr } from "@/utils/index";
import IconsDialog from "./IconsDialog";
import {
  inputComponents,
  selectComponents,
  // layoutComponents,
} from "@/utils/generator/config";

const isArray = Array.isArray;

const dateTimeFormat = {
  date: "yyyy-MM-dd",
  week: "yyyy  WW ",
  month: "yyyy-MM",
  year: "yyyy",
  datetime: "yyyy-MM-dd HH:mm:ss",
  daterange: "yyyy-MM-dd",
  monthrange: "yyyy-MM",
  datetimerange: "yyyy-MM-dd HH:mm:ss",
};

export default {
  components: {
    draggable,
    TreeNodeDialog,
    IconsDialog,
  },
  props: ["showField", "activeData", "formConf"],
  data() {
    return {
      activeData_: this.activeData,
      formConf_: this.formConf,

      currentTab: "field",
      currentNode: null,
      dialogVisible: false,
      iconsVisible: false,
      currentIconModel: null,
      dateTypeOptions: [
        {
          label: "(date)",
          value: "date",
        },
        {
          label: "(week)",
          value: "week",
        },
        {
          label: "(month)",
          value: "month",
        },
        {
          label: "(year)",
          value: "year",
        },
        {
          label: "(datetime)",
          value: "datetime",
        },
      ],
      dateRangeTypeOptions: [
        {
          label: "(daterange)",
          value: "daterange",
        },
        {
          label: "(monthrange)",
          value: "monthrange",
        },
        {
          label: "(datetimerange)",
          value: "datetimerange",
        },
      ],
      colorFormatOptions: [
        {
          label: "hex",
          value: "hex",
        },
        {
          label: "rgb",
          value: "rgb",
        },
        {
          label: "rgba",
          value: "rgba",
        },
        {
          label: "hsv",
          value: "hsv",
        },
        {
          label: "hsl",
          value: "hsl",
        },
      ],
      justifyOptions: [
        {
          label: "start",
          value: "start",
        },
        {
          label: "end",
          value: "end",
        },
        {
          label: "center",
          value: "center",
        },
        {
          label: "space-around",
          value: "space-around",
        },
        {
          label: "space-between",
          value: "space-between",
        },
      ],
      layoutTreeProps: {
        label(data) {
          return data.componentName || `${data.label}: ${data.vModel}`;
        },
      },
    };
  },
  watch: {
    activeData(newVal) {
      this.activeData_ = newVal;
    },
    // eslint-disable-next-line no-unused-vars
    activeData_(newVal) {
      // this.$emit("change-activeData", newVal);
    },
  },
  computed: {
    documentLink() {
      return (
        this.activeData_.document ||
        "https://element.eleme.cn/#/zh-CN/component/installation"
      );
    },
    dateOptions() {
      if (
        this.activeData_.type !== undefined &&
        this.activeData_.tag === "el-date-picker"
      ) {
        if (this.activeData_["start-placeholder"] === undefined) {
          return this.dateTypeOptions;
        }
        return this.dateRangeTypeOptions;
      }
      return [];
    },
    tagList() {
      return [
        {
          label: "",
          options: inputComponents,
        },
        {
          label: "",
          options: selectComponents,
        },
      ];
    },
  },
  created() {
    console.log(this.activeData);
    console.log(this.activeData_);
  },
  methods: {
    addReg() {
      this.activeData_.regList.push({
        pattern: "",
        message: "",
      });
    },
    addSelectItem() {
      this.activeData_.options.push({
        label: "",
        value: "",
      });
    },
    addTreeItem() {
      ++this.idGlobal;
      this.dialogVisible = true;
      this.currentNode = this.activeData_.options;
    },
    renderContent(h, { node, data }) {
      return (
        <div class="custom-tree-node">
          <span>{node.label}</span>
          <span class="node-operation">
            <i
              on-click={() => this.append(data)}
              class="el-icon-plus"
              title=""
            ></i>
            <i
              on-click={() => this.remove(node, data)}
              class="el-icon-delete"
              title=""
            ></i>
          </span>
        </div>
      );
    },
    append(data) {
      if (!data.children) {
        this.$set(data, "children", []);
      }
      this.dialogVisible = true;
      this.currentNode = data.children;
    },
    remove(node, data) {
      const { parent } = node;
      const children = parent.data.children || parent.data;
      const index = children.findIndex((d) => d.id === data.id);
      children.splice(index, 1);
    },
    addNode(data) {
      this.currentNode.push(data);
    },
    setOptionValue(item, val) {
      item.value = isNumberStr(val) ? +val : val;
    },
    setDefaultValue(val) {
      if (Array.isArray(val)) {
        return val.join(",");
      }
      if (["string", "number"].indexOf(val) > -1) {
        return val;
      }
      if (typeof val === "boolean") {
        return `${val}`;
      }
      return val;
    },
    onDefaultValueInput(str) {
      if (isArray(this.activeData_.defaultValue)) {
        // 
        this.$set(
          this.activeData_,
          "defaultValue",
          str.split(",").map((val) => (isNumberStr(val) ? +val : val))
        );
      } else if (["true", "false"].indexOf(str) > -1) {
        // 
        this.$set(this.activeData_, "defaultValue", JSON.parse(str));
      } else {
        // 
        this.$set(
          this.activeData_,
          "defaultValue",
          isNumberStr(str) ? +str : str
        );
      }
    },
    onSwitchValueInput(val, name) {
      if (["true", "false"].indexOf(val) > -1) {
        this.$set(this.activeData_, name, JSON.parse(val));
      } else {
        this.$set(this.activeData_, name, isNumberStr(val) ? +val : val);
      }
    },
    setTimeValue(val, type) {
      const valueFormat = type === "week" ? dateTimeFormat.date : val;
      this.$set(this.activeData_, "defaultValue", null);
      this.$set(this.activeData_, "value-format", valueFormat);
      this.$set(this.activeData_, "format", val);
    },
    spanChange(val) {
      this.formConf_.span = val;
    },
    multipleChange(val) {
      this.$set(this.activeData_, "defaultValue", val ? [] : "");
    },
    dateTypeChange(val) {
      this.setTimeValue(dateTimeFormat[val], val);
    },
    rangeChange(val) {
      this.$set(
        this.activeData_,
        "defaultValue",
        val
          ? [this.activeData_.min, this.activeData_.max]
          : this.activeData_.min
      );
    },
    rateTextChange(val) {
      if (val) this.activeData_["show-score"] = false;
    },
    rateScoreChange(val) {
      if (val) this.activeData_["show-text"] = false;
    },
    colorFormatChange(val) {
      this.activeData_.defaultValue = null;
      this.activeData_["show-alpha"] = val.indexOf("a") > -1;
      this.activeData_.renderKey = +new Date(); // renderKey,
    },
    openIconsDialog(model) {
      this.iconsVisible = true;
      this.currentIconModel = model;
    },
    setIcon(val) {
      this.activeData_[this.currentIconModel] = val;
    },
    tagChange(tagIcon) {
      let target = inputComponents.find((item) => item.tagIcon === tagIcon);
      if (!target)
        target = selectComponents.find((item) => item.tagIcon === tagIcon);
      this.$emit("tag-change", target);
    },
  },
};
</script>

